@font-face {
  font-family: Comfortaa;
  src: local("Comfortaa"), url("../fonts/Comfortaa-Regular.woff2") format("woff2"), url("../fonts/Comfortaa-Regular.woff") format("woff");
  font-weight: 400;
  display: swap;
}
@font-face {
  font-family: "Helvetica Neue Cyr";
  src: local("Helvetica Neue Cyr Light"), local("Helvetica-Neue-Cyr-Light"), url("../fonts/HelveticaNeueCyr-Light.woff2") format("woff2"), url("../fonts/HelveticaNeueCyr-Light.woff") format("woff");
  font-weight: 300;
  display: swap;
}
@font-face {
  font-family: "Helvetica Neue Cyr";
