  font-weight: 400;
  display: swap;
}
@font-face {
  font-family: "Helvetica Neue Cyr";
  src: local("Helvetica Neue Cyr Medium"), local("Helvetica-Neue-Cyr-Medium"), url("../fonts/HelveticaNeueCyr-Medium.woff2") format("woff2"), url("../fonts/HelveticaNeueCyr-Medium.woff") format("woff");
  font-weight: 500;
  display: swap;
}
