@media (max-width: 1024px) {
  .slider {
    position: relative;
  }
  .slider__item {
    display: none;
  }
  .slider__item_active {
    display: flex;
  }
  .slider__left {
    z-index: 2;
    position: absolute;
    content: "";
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    width: 54px;
    height: 54px;
    background: url("../../image/pets/left.svg") center/contain no-repeat;
  }
  .slider__right {
    z-index: 2;
    position: absolute;
    content: "";
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    width: 54px;
    height: 54px;
    background: url("../../image/pets/right.svg") center/contain no-repeat;
  }
}
@media (max-width: 768px) {
  .slider__left {
    width: 26px;
    height: 26px;
    transform: translate(-50%, -50%);
  }
  .slider__right {
    width: 26px;
    height: 26px;
    transform: translate(50%, -50%);
  }
}
* {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

html {
  position: relative;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  background-color: #f2f0f1;
  font-family: "Helvetica Neue Cyr", sans-serif;
  font-size: 20px;
  font-weight: 300;
  color: #282626;
}

a {
  color: inherit;
  text-decoration: none;
  transition: 0.2s;
}

a:hover {
  text-shadow: 0 0 5px;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  display: block;
}

.container {
  max-width: 1300px;
  padding: 0px 60px;
  margin: 0 auto;
}

.section {
  padding: 45px 0;
}

.section__title {
  color: #282626;
  font-family: Comfortaa, sans-serif;
  font-size: 50px;
  font-weight: 400;
  line-height: 60px;
  margin-bottom: 33px;
}

.section__text {
  max-width: 447px;
}

.section__text_wide {
  max-width: 451px;
}

.section__text {
  line-height: 30px;
  margin-bottom: 20px;
}

.section__title {
  font-size: 30px;
  font-weight: 400;
  line-height: 40px;
}

.section__text_bold {
  font-family: "Helvetica Neue Cyr", sans-serif;
  font-weight: 500;
  line-height: 30px;
}

.button {
  width: 80px;
  height: 90px;
  border: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: 550;
  font-size: 14px;
  line-height: 22px;
  text-transform: uppercase;
  color: #FF8A00;
  background-color: transparent;
  cursor: pointer;
}

.button img {
  transition: 0.2s;
}

.button:hover img,
.button:active img,
.button:focus img {
  filter: drop-shadow(0px 4px 6px);
  outline: none;
}

.link {
  align-self: flex-end;
  font-family: HelveticaNeueCyr;
  font-weight: 550;
  font-size: 20px;
  line-height: 26px;
  color: #FF8A00;
  padding-right: 45px;
  background: url("../image/about/arrow.svg") center right no-repeat;
}

@media (max-width: 1024px) {
  .container {
    padding: 0px 35px;
  }
}
@media (max-width: 769px) {
  .container {
    padding: 0px 25px;
  }
}
@media (max-width: 480px) {
  .container {
    padding: 0px 25px;
  }
}
.header {
  background-color: #fff;
  padding-top: 25px;
  padding-bottom: 27px;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__menu {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 21px;
  cursor: pointer;
  margin-right: 150px;
  transition: 0.2s;
}

.header__menu:hover {
  opacity: 0.6;
}

.header__line {
  height: 3px;
  width: 40px;
  background: #282626;
  border-radius: 1px;
}

.header__line:before {
  position: absolute;
  content: "";
  width: 100%;
  height: 3px;
  top: 0;
  left: 0;
  background: #282626;
  border-radius: 1px;
}

.header__line:after {
  position: absolute;
  content: "";
  width: 29px;
  height: 3px;
  bottom: 0;
  left: 0;
  background: #282626;
  border-radius: 1px;
}

.header__phone {
  position: relative;
  font-weight: 550;
  font-size: 20px;
  line-height: 30px;
  padding-left: 35px;
}

.header__phone:before {
  position: absolute;
  content: "";
  width: 18px;
  height: 18px;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  background: url("../image/header/phone.svg");
}

.header__phone_mob {
  display: none;
}

@media (max-width: 900px) {
  .logo {
    max-width: 300px;
  }

  .header__menu {
    margin-right: 0;
  }

  .header__phone {
    display: none;
  }
  .header__phone_mob {
    display: block;
    padding-left: 73px;
  }
  .header__phone_mob:before {
    left: 42px;
  }
}
@media (max-width: 480px) {
  .logo {
    max-width: 150px;
  }

  .header__phone_mob {
    display: block;
    padding-left: 41px;
    font-size: 14px;
    line-height: 20px;
  }
  .header__phone_mob:before {
    width: 18px;
    height: 18px;
    left: 13px;
  }
}
.burger {
  z-index: 3;
  position: absolute;
  width: 394px;
  height: 394px;
  top: 115px;
  background: #F2F0F1;
  border-bottom: 10px solid #FF8A00;
  transform: translate(-600px, -600px);
  transition: 0.5s;
}

.burger__active {
  transform: translate(0, 0);
}

.burger__list {
  position: relative;
  padding-top: 70px;
  font-weight: 550;
  font-size: 20px;
  line-height: 30px;
}

.burger__list:before {
  position: absolute;
  content: "";
  width: 52px;
  height: 30px;
  background: url("../image/header/corner.svg");
  left: 0;
  top: 2px;
  transform: translateY(-100%);
  border-bottom: 3px solid #F2F0F1;
}

.burger__item {
  position: relative;
  padding-left: 39px;
  margin-bottom: 31px;
}

.burger__item:not(:last-child):after {
  position: absolute;
  content: "";
  width: 334px;
  height: 1px;
  background-color: #FF8A00;
  bottom: -15px;
  left: 20px;
}

@media (max-width: 900px) {
  .burger {
    width: 349px;
    height: 388px;
  }
  .burger__list {
    padding-top: 40px;
    font-size: 18px;
    line-height: 24px;
  }
  .burger__item:not(:last-child):after {
    width: 309px;
  }
}
@media (max-width: 480px) {
  .burger {
    top: 80px;
    width: 270px;
    height: 326px;
  }
  .burger__list {
    padding-top: 20px;
    font-size: 14px;
    line-height: 20px;
  }
  .burger__list:before {
    display: none;
  }
  .burger__item {
    padding-left: 10px;
  }
  .burger__item:not(:last-child):after {
    width: 100%;
    left: 0;
  }
}
.hero {
  padding-top: 0;
  padding-bottom: 90px;
  background-color: #fff;
}

.hero__container {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  max-height: 580px;
  align-content: space-between;
}

.hero__info {
  z-index: 2;
  position: relative;
  width: 180px;
}

.hero__image {
  position: absolute;
  width: 580px;
  height: 580px;
  left: 50%;
  transform: translateX(-50%);
}

.hero__title {
  z-index: 2;
  font-family: Comfortaa;
  font-size: 40px;
  line-height: 60px;
  max-width: 440px;
  font-weight: 300;
  margin-bottom: 80px;
  padding-top: 137px;
}

.hero__title_orange {
  color: #FF8A00;
  font-weight: normal;
  font-size: 60px;
}

.hero__button {
  width: 180px;
  height: 155px;
  margin-left: 100px;
}

.hero__list {
  padding-top: 100px;
  display: flex;
  flex-direction: column;
  gap: 30px;
  margin-right: 100px;
}

.hero__item {
  height: 80px;
  padding-left: 100px;
  display: flex;
  align-items: center;
  cursor: pointer;
  border: 1px solid transparent;
}

.hero__item:hover {
  border: 1px solid #FF8A00;
}

.hero__item:active {
  border: 1px solid #FF8A00;
  color: #FF8A00;
}

.hero__item_sara {
  background: url("../image/hero/sara.png") no-repeat;
}

.hero__item_boris {
  background: url("../image/hero/boris.png") no-repeat;
}

.hero__item_lui {
  background: url("../image/hero/lui.png") no-repeat;
}

.hero__item_piter {
  background: url("../image/hero/piter.png") no-repeat;
}

.hero__item_kenni {
  background: url("../image/hero/kenni.png") no-repeat;
}

.hero__item_show {
  display: none;
}

@media (max-width: 1200px) {
  .hero__image {
    position: absolute;
    width: 416px;
    height: 416px;
    left: 0;
    transform: translateX(330px);
  }

  .hero__title {
    padding-top: 100px;
    font-size: 30px;
    max-width: 320px;
    font-weight: 300;
    margin-bottom: 60px;
    line-height: 40px;
  }

  .hero__title_orange {
    font-size: 44px;
    line-height: 54px;
  }

  .hero__button {
    width: 148px;
    height: 126px;
    margin-left: 50px;
  }

  .hero__list {
    padding-top: 20px;
    display: flex;
    flex-direction: column;
    gap: 30px;
    margin-right: 0;
  }

  .hero__item {
    height: 80px;
    padding-left: 100px;
    display: flex;
    align-items: center;
    cursor: pointer;
  }

  .hero__item_show {
    display: none;
  }
}
@media (max-width: 950px) {
  .hero {
    padding-bottom: 132px;
  }

  .hero__container {
    max-height: 700px;
    flex-direction: row;
    justify-content: space-between;
  }

  .hero__info {
    display: flex;
    flex-direction: column;
    max-width: unset;
    padding-top: 60px;
  }

  .hero__image {
    position: absolute;
    width: 416px;
    height: 416px;
    top: 75px;
    left: 50%;
    transform: translateX(-50%);
  }

  .hero__title {
    padding-top: 30px;
    font-size: 30px;
    width: 100%;
    max-width: unset;
    line-height: 40px;
    margin-bottom: 90px;
  }

  .hero__button {
    width: 148px;
    height: 126px;
    margin-left: 0;
  }

  .hero__list {
    padding-top: 0;
    align-self: flex-end;
    flex-direction: column;
    gap: 30px;
  }

  .hero__item {
    max-width: 140px;
    height: 60px;
    padding-left: 80px;
    background-size: contain;
    font-size: 14px;
    line-height: 20px;
  }

  .hero__item_show {
    display: none;
  }
}
@media (max-width: 768px) {
  .hero__container {
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
  }

  .hero__title {
    margin-bottom: 330px;
  }

  .hero__info {
    order: 1;
    padding-top: 30px;
  }

  .hero__image {
    width: 300px;
    height: 300px;
  }

  .hero__list {
    flex-direction: row;
    align-self: unset;
  }

  .hero__button {
    width: 148px;
    height: 126px;
  }
}
@media (max-width: 570px) {
  .hero {
    padding-bottom: 40px;
  }

  .hero__title {
    font-size: 20px;
    line-height: 30px;
  }

  .hero__title_orange {
    font-size: 26px;
  }

  .hero__info {
    order: 1;
    padding-top: 30px;
    max-width: 84px;
  }

  .hero__image {
    top: 110px;
    width: 270px;
    height: 270px;
  }

  .hero__list {
    flex-direction: row;
    align-self: unset;
    gap: 10px;
  }

  .hero__item {
    padding-left: 0;
    padding-top: 65px;
    width: 60px;
    height: 85px;
    justify-content: center;
  }

  .hero__button {
    width: 84px;
    height: 78px;
    font-size: 8px;
    line-height: 12px;
  }

  .hero__button img {
    max-width: 50px;
  }
}
