.contact {
  padding: 90px 0;
  background-color: #fff;
}

.contact__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #F2F0F1;
  border-radius: 20px;
  padding: 50px 100px;
  max-width: 780px;
  margin: auto;
}

.contact__title {
  font-family: Comfortaa;
  font-weight: bold;
  font-size: 30px;
  line-height: 40px;
  color: #FF8A00;
}

.contact__text {
  font-size: 20px;
  line-height: 30px;
  max-width: 100%;
  margin-bottom: 0;
  text-align: center;
  margin-bottom: 20px;
}

.contact__text_orange {
  font-weight: 550;
  color: #FF8A00;
}

.contact__contacts {
  display: flex;
  justify-content: space-between;
  min-width: 540px;
  margin-bottom: 60px;
}

.contact__link {
  position: relative;
  font-weight: 550;
}

.contact__phone {
  padding-left: 38px;
}

.contact__phone:before {
  position: absolute;
  content: "";
  width: 18px;
  height: 18px;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background: url("../image/form/phone.svg") center/contain no-repeat;
}

.contact__email:before {
  position: absolute;
  content: "";
  width: 18px;
  height: 18px;
  top: 50%;
  left: -38px;
  transform: translateY(-50%);
  background: url("../image/form/envelope.svg") center/contain no-repeat;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form__text {
  max-width: 100%;
  margin-bottom: 20px;
}

.form__inputs {
  display: flex;
  margin-bottom: 34px;
}

.form__wrap {
  display: flex;
  flex-direction: column;
  width: 280px;
}

.form__wrap_phone {
  margin-right: 20px;
}

.form__label {
  font-size: 12px;
  line-height: 14px;
}

.form__input {
  height: 40px;
  border: none;
  padding-left: 15px;
  outline: none;
}

.form__input:hover {
  border: 1px inset #CBCBCB;
}

.form__input:active,
.form__input:focus {
  border: 1px inset #282626;
}

.form__button {
  width: 80px;
  height: 90px;
  font-weight: 550;
  font-size: 12px;
  line-height: 16px;
  align-self: flex-end;
}

.form__button img {
  width: 73px;
  margin-bottom: 5px;
}

@media (max-width: 900px) {
  .contact__container {
    max-width: 540px;
  }
  .contact__title {
    font-size: 18px;
    line-height: 24px;
  }
  .contact__text {
    font-size: 14px;
    line-height: 20px;
    text-align: center;
  }
  .contact__contacts {
    min-width: 400px;
    margin-bottom: 33px;
  }
  .contact__link {
    font-size: 14px;
    line-height: 20px;
  }

  .form__text {
    text-align: center;
    font-size: 14px;
    line-height: 20px;
  }
  .form__inputs {
    margin-bottom: 20px;
  }
  .form__wrap {
    width: 200px;
  }
  .form__label {
    margin-bottom: 5px;
  }
}
@media (max-width: 600px) {
  .contact {
    padding-top: 20px;
  }
  .contact__container {
    padding: 30px;
    max-width: 270px;
  }
  .contact__title {
    font-size: 16px;
    line-height: 22px;
  }
  .contact__text {
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 10px;
  }
  .contact__contacts {
    flex-direction: column;
    gap: 15px;
    min-width: 200px;
    margin-bottom: 33px;
  }
  .contact__link {
    font-size: 14px;
    line-height: 20px;
    padding-left: 0;
    text-align: center;
  }
  .contact__phone:before {
    left: 15px;
  }
  .contact__email {
    padding-left: 32px;
  }
  .contact__email:before {
    left: 0;
  }

  .form__inputs {
    flex-direction: column;
    gap: 14px;
    margin-bottom: 20px;
  }
  .form__wrap_phone {
    margin-right: 0;
  }
  .form__label {
    font-size: 8px;
    line-height: 10px;
  }
  .form__button {
    align-self: center;
  }
}
.footer {
  padding: 90px 0;
  background-color: #282626;
  color: #ffffff;
}
.footer__container {
  display: grid;
  grid-template-areas: "map address" "info socials";
  gap: 90px 120px;
}
.footer__map-wrap {
  grid-area: map;
  width: 680px;
  height: 360px;
}
.footer__address {
  grid-area: address;
  max-width: 320px;
  font-style: unset;
  padding-top: 65px;
}
.footer__info {
  grid-area: info;
  max-width: 440px;
}
.footer__socials {
  grid-area: socials;
  max-width: 280px;
}
.footer__title {
  color: #ffffff;
  font-weight: bold;
  font-size: 40px;
  line-height: 40px;
  margin-bottom: 8px;
}
.footer__subtitle {
  font-weight: 300;
  font-size: 20px;
  line-height: 26px;
  margin-bottom: 20px;
}
.footer__text {
  font-weight: 300;
  font-size: 14px;
  line-height: 18px;
  margin-bottom: 30px;
}
.footer__contacts {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.footer_-link {
  position: relative;
  font-weight: 550;
  font-size: 20px;
  line-height: 30px;
  padding-left: 35px;
}
.footer__phone::before {
  position: absolute;
  content: "";
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 18px;
  height: 18px;
  background: url("../../image/form/phone.svg") center/contain no-repeat;
}
.footer__email::before {
  position: absolute;
  content: "";
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 20px;
  height: 16px;
  background: url("../../image/form/envelope.svg") center/contain no-repeat;
}
.footer__info {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.footer__copy {
  font-weight: 300;
  font-size: 20px;
  line-height: 26px;
}
.footer__socials {
  display: flex;
  flex-direction: column;
}
.footer__socials-link {
  width: 38px;
  height: 38px;
  display: block;
}
.footer__list {
  display: flex;
  gap: 40px;
  margin-bottom: 20px;
}
.footer__item {
  width: 38px;
  height: 38px;
  border-radius: 50%;
  transition: 0.2s;
}
.footer__item-inst {
  background: url("../../image/footer/inst.svg") center/contain no-repeat;
}
.footer__item-vk {
  background: url("../../image/footer/vk.svg") center/contain no-repeat;
}
.footer__item-fb {
  background: url("../../image/footer/fb.svg") center/contain no-repeat;
}
.footer__item-tg {
  background: url("../../image/footer/tg.svg") center/contain no-repeat;
}
.footer__item:hover {
  background-color: rgba(255, 138, 0, 0.3);
}

