.modal {
  z-index: 3;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.67);
  display: none;
  justify-content: center;
  align-items: center;
  opacity: 0;
}

.modal_active {
  display: flex;
  opacity: 1;
}

.modal__form {
  display: flex;
  flex-direction: column;
}